import { CodeIcon } from "@heroicons/react/solid";
import React from "react";
import { motion } from "framer-motion";
import { projects } from "../data";

export default function Projects() {
    const [filter, setFilter] = React.useState('all');
    const filteredProjects = projects.filter(project => 
        filter === 'all' || project.subtitle.toLowerCase().includes(filter)
    );

    return (
        <section id="projects" className="text-gray-400 bg-gray-900 body-font">
            <div className="container px-5 py-10 mx-auto text-center lg:px-40">
                <motion.div 
                    className="flex flex-col w-full mb-20"
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.5 }}
                >
                    <CodeIcon className="mx-auto inline-block w-10 mb-4" />
                    <h1 className="sm:text-4xl text-3xl font-medium title-font mb-4 text-white">
                        Apps I've Built
                    </h1>
                    <p className="lg:w-2/3 mx-auto leading-relaxed text-base">
                        This is a list of apps I've built. I've built them using Full-Stack Javascript, EJS, Bootstrap and Tailwind CSS. I've also used MongoDB for database storage.
                    </p>
                </motion.div>
                <div className="flex justify-center mb-8 space-x-4">
                    <button 
                        onClick={() => setFilter('all')}
                        className={`px-4 py-2 rounded transition-colors duration-200 ${filter === 'all' ? 'bg-green-500 text-white' : 'bg-gray-800'}`}>
                        All
                    </button>
                    <button 
                        onClick={() => setFilter('mern')}
                        className={`px-4 py-2 rounded transition-colors duration-200 ${filter === 'mern' ? 'bg-green-500 text-white' : 'bg-gray-800'}`}>
                        MERN Stack
                    </button>
                    <button 
                        onClick={() => setFilter('node')}
                        className={`px-4 py-2 rounded transition-colors duration-200 ${filter === 'node' ? 'bg-green-500 text-white' : 'bg-gray-800'}`}>
                        Node.js
                    </button>
                </div>
                <div className="flex flex-wrap -m-4">
                    {filteredProjects.map((project, index) => (
                        <motion.a
                            href={project.link}
                            key={project.image}
                            className="sm:w-1/2 w-100 p-4"
                            initial={{ opacity: 0, y: 20 }}
                            animate={{ opacity: 1, y: 0 }}
                            transition={{ duration: 0.5, delay: index * 0.1 }}
                            target="_blank"
                            rel="noopener noreferrer"
                        >
                            <div className="flex relative h-[300px]">
                                <img
                                    alt={project.title}
                                    className="absolute inset-0 w-full h-full object-cover object-center rounded-lg"
                                    src={project.image}
                                    loading="lazy"
                                />
                                <div className="px-8 py-10 relative z-10 w-full border-4 border-gray-800 bg-gray-900 opacity-0 hover:opacity-100 transition-opacity duration-300 rounded-lg backdrop-blur-sm">
                                    <h2 className="tracking-widest text-sm title-font font-medium text-green-400 mb-1">
                                        {project.subtitle}
                                    </h2>
                                    <h1 className="title-font text-lg font-medium text-white mb-3">
                                        {project.title}
                                    </h1>
                                    <p className="leading-relaxed">{project.description}</p>
                                    {project.technologies && (
                                        <div className="mt-4 flex flex-wrap gap-2">
                                            {project.technologies.map((tech, i) => (
                                                <span key={i} className="px-2 py-1 text-sm bg-gray-800 rounded-full">
                                                    {tech}
                                                </span>
                                            ))}
                                        </div>
                                    )}
                                </div>
                            </div>
                        </motion.a>
                    ))}
                </div>
            </div>
        </section>
    );
}